.img_fullsize-container {
  width: 90%;
  margin-left: 5%;
  display: flex;
  justify-content: center;
}
.img_fullsize-img.portrait {
  width: 45%;
}
.img_fullsize-img.landscape {
  width: 100%;
}
.img_container {
  margin-top: 20px;
  width: 100%;
}
.img_select-img-container {
  width: 90%;
  margin-left: 5%;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  overflow-y: scroll;
  margin-top: 5px;
}
.img_select-img-box {
  width: 100px;
  padding-right: 5px;
}
.img_select-img {
  width: 100px;
  height: 80px;
  object-fit: cover;
}
.img_select-img.active {
  border: 2px solid #1f90ed;
  border-radius: 5px;
}
